.home{
    /* border: 1px solid red; */
    width: 100%;
    height: auto;
    display: flex;
    
}

.home>div:nth-child(1){
    width: 30%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */

}
.home>div:nth-child(2){
  
    width: 70%;
    font-weight: bold;
    text-align: left;
    padding: 10% 10%;
    /* border: 1px solid blue; */
}
.home>div:nth-child(1)>img{
   border-radius: 5px;
    width: 60%;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}
.home>div:nth-child(2)>p{
    font-weight: 400;
}